import React from 'react';
import VRLogo from './VRLogo.js';
import Quote from './Quote.js';

class LeftVerticalLink extends React.Component {
    render() {
        return (
            <div orientation="left" className="d-none d-lg-block Left-Vertical">
                <ul className="Left-Vertical-Inner fade-enter-done">
                <Quote />
                <li ><a href="https://github.com/VeenaRengarajan" aria-label="GitHub"><i className="fa fa-github fa-lg"></i></a></li>
                <VRLogo />
                <li ><a href="https://www.linkedin.com/in/veenarengarajan/" aria-label="Linkedin"><i className="fa fa-linkedin fa-lg"></i></a></li>
                </ul>
          </div>
        );
    }
}

export default LeftVerticalLink;