import React from 'react';
import background from '../assests/images/Bg.jpg';
import Carousel from 'react-bootstrap/Carousel'
import ProgressBar from 'react-bootstrap/ProgressBar'
import anime from 'animejs/lib/anime.es.js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSmileWink, faRunning, faGrinTongueWink, faThumbsUp, faGrinBeamSweat, faBookReader, faPeopleCarry, faSmileBeam, faSurprise, faLaughBeam, faHandHoldingHeart, faSpinner, faAtom} from '@fortawesome/free-solid-svg-icons'

class IntroImg extends React.Component {
    componentDidMount() {
        // start new code 
        const searchBarTimer = setInterval(function() {
            // method to be executed;
            const typed = document.getElementById('typed').innerHTML;
            for (var i = 0; i < 6; i++) {
                let flag = false;
                if (typed !== null && typed !== undefined && typed == document.getElementById('typed-strings').getElementsByTagName('p')[i].innerHTML) {
                    document.getElementById('typed').innerHTML = document.getElementById('typed-strings').getElementsByTagName('p')[i+1].innerHTML;
                    flag = true;
                } else if (typed.includes('Hi....')) {
                    document.getElementById('typed').innerHTML = document.getElementById('typed-strings').getElementsByTagName('p')[i].innerHTML;
                    flag = true;
                } else if (typed.includes('Start scrolling already!')) {
                    document.getElementById('typed').innerHTML = 'Hi....';
                    flag = true;
                }
                if (flag) {
                    break;
                }
            }
          }, 3000);
        // end new code
        // start new code
        const bgTimer = setInterval(function() {
            document.getElementById('introImg').style.backgroundImage = "url("+background+")";
            document.getElementById('loading').style.display = 'none';
            document.getElementById('searchBar').style.opacity = '1'; 
            document.getElementById('marginId').style.opacity = '1'; 
            document.getElementById('carouselId').style.opacity = '1';  
            document.getElementById('catchMeAnimation').style.opacity = '1'; 

          }, 3000);
        // end new code
        //name animation 
        var card = document.querySelector(".custom");
        var playing = false;

        card.addEventListener('click',function() {
        if(playing)
            return;
        
        playing = true;
        anime({
            targets: card,
            scale: [{value: 1}, {value: 1.4}, {value: 1, delay: 250}],
            rotateY: {value: '+=180', delay: 200},
            easing: 'easeInOutSine',
            duration: 400,
            complete: function(anim){
            playing = false;
            }
        });
        });
        anime({
            targets: card,
            keyframes: [
              {translateY: -40},
              {translateX: 900},
              {translateY: 350},
              {translateX: 400},
              {translateY: 150},
              {translateY: 500},
              {translateX: -20},
            ],
            duration: 4000,
            easing: 'easeOutElastic(1, .8)',
            loop: true
          });

          function animateValue(obj, start, end, duration) {
            let startTimestamp = null;
            const step = (timestamp) => {
              if (!startTimestamp) startTimestamp = timestamp;
              const progress = Math.min((timestamp - startTimestamp) / duration, 1);
              obj.innerHTML = Math.floor(progress * (end - start) + start);
              if (progress < 1) {
                window.requestAnimationFrame(step);
              }
            };
            window.requestAnimationFrame(step);
          }
          
          const obj = document.getElementById("value");
          animateValue(obj, 5, 0, 5000);
    }
    render() {
        return (
            <div id="introImg" className="App-logo">
                {/* name animation */}
                <div id="catchMeAnimation" className="custom-container" style={{opacity: '0',}}>
                    <div className="custom">
                        <div className="front">
                            <p style={{marginLeft: '8px',}}><FontAwesomeIcon icon={faRunning} />..Catch me..<FontAwesomeIcon icon={faSmileWink} /></p>
                        </div>
                        <div className="back">
                        <p style={{marginLeft: '8px',}}><FontAwesomeIcon icon={faThumbsUp} />..Now Again..<FontAwesomeIcon icon={faGrinTongueWink} /></p>
                        </div>
                    </div>
                </div>
                {/* name animation */}
                <div id="loading" style={{maxWidth: '350px', paddingBottom: '10px', paddingTop: '180px'}}>
                    <ProgressBar animated now={75} style={{backgroundColor: '#141515!important'}}/>
                    <div id="loadingText" style={{fontWeight: 'bold', fontSize: '18px', color: 'black'}}>Welcome <FontAwesomeIcon icon={faSmileBeam} />.. Updating <FontAwesomeIcon  className="fa-spin" icon={faSpinner} /> Wait just <span id="value" className="counter">5</span>.</div>
                </div>
                <div id="searchBar" className="home-search-bar" style={{ opacity: '0'}}>
                    <div id="typed-strings" style={{ display: 'none'}}>
                            <p style={{ display: 'none'}}>My name is Veena Rengarajan <FontAwesomeIcon icon={faHandHoldingHeart} /></p>
                            <p style={{ display: 'none'}}>I am kind of a Geek <FontAwesomeIcon icon={faGrinBeamSweat} />!</p>
                            <p style={{ display: 'none'}}>I enjoy working on challenges <FontAwesomeIcon className="fa-pulse" icon={faAtom} />..</p>
                            <p style={{ display: 'none'}}>I love learning new things <FontAwesomeIcon icon={faBookReader} />..</p>
                            <p style={{ display: 'none'}}>Think we could work together <FontAwesomeIcon icon={faPeopleCarry} />?</p>
                            <p style={{ display: 'none'}}>Want to chat? Well why wait <FontAwesomeIcon icon={faSurprise} />!</p>
                            <p style={{ display: 'none'}}>Start scrolling already! <FontAwesomeIcon icon={faSmileBeam} /></p>
                            {/* <p style={{ display: 'none'}}>I am a Web Analytics Consultant</p>
                            <p style={{ display: 'none'}}>I do like to measure everything</p>
                            <p style={{ display: 'none'}}>We could work together?</p>
                            <p style={{ display: 'none'}}>Still here waiting?</p>
                            <p style={{ display: 'none'}}>Start scrolling already!</p> */}
                        <p>Hi....</p>
                    </div>
                    <span id="typed" className="typed">Hi....<FontAwesomeIcon icon={faLaughBeam} /></span><span className="typed-cursor">|</span>
                </div>
                <div id="marginId" style={{ marginTop: '5px', marginBottom: '10px', maxWidth: '350px', borderBottom: '2px solid', borderBottomColor: 'black', opacity: '0'}}></div>
                <Carousel id="carouselId" controls={false} fade={true} style={{width: '350px', textAlign: 'center', opacity: '0'}} >
                    <Carousel.Item interval={10}>
                    <span>A Full Stack Developer</span>
                    </Carousel.Item>
                    <Carousel.Item interval={10}>
                    <span>Cloud and AI enthusiast</span>
                    </Carousel.Item>
                    <Carousel.Item interval={10}>
                    <span>Creative Innovator</span>
                    </Carousel.Item>
                    <Carousel.Item interval={10}>
                    <span>Passionate Software Engineer</span>
                    </Carousel.Item>
                </Carousel>
            </div> 
        );
    }
}

export default IntroImg;