import React from 'react';
import anime from 'animejs/lib/anime.es.js';

import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
// import Form from 'react-bootstrap/Form'
// import Button from 'react-bootstrap/Button'

class GlobalHeader extends React.Component {
    componentDidMount() {
        var textWrapper = document.querySelector('.ml11 .letters');
        textWrapper.innerHTML = textWrapper.textContent.replace(/([^\x00-\x80]|\w)/g, "<span className='letter'>$&</span>");

        anime.timeline({loop: true})
        .add({
            targets: '.ml11 .line',
            scaleY: [0,1],
            opacity: [0.5,1],
            easing: "easeOutExpo",
            duration: 700
        })
        .add({
            targets: '.ml11 .line',
            translateX: [0, document.querySelector('.ml11 .letters').getBoundingClientRect().width + 10],
            easing: "easeOutExpo",
            duration: 700,
            delay: 100
        }).add({
            targets: '.ml11 .letter',
            opacity: [0,1],
            easing: "easeOutExpo",
            duration: 600,
            offset: '-=570',
            delay: (el, i) => 34 * (i+1)
        }).add({
            targets: '.ml11',
            opacity: 0,
            duration: 1000,
            easing: "easeOutExpo",
            delay: 1000
        });

    }
    
    render() {
        return (
            <Navbar fixed="top" style={{ backgroundColor: '#282c34', }} expand="lg">
                <h1 className="ml11">
                    <span className="text-wrapper" style={{color:'#ccd6f6',}}>
                    <span className="line line1"></span>
                    <span className="letters">Veena Rengarajan</span>
                    </span>
                </h1>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ml-auto" >
                    <Nav.Link fill="true" href="#about" className="NavHeader" style={{color:'#ccd6f6',}}>About Me</Nav.Link>
                    <Nav.Link fill="true" href="#jobs" className="NavHeader" style={{color:'#ccd6f6',}}>My Journey</Nav.Link>
                    <Nav.Link fill="true" href="#skills" className="NavHeader" style={{color:'#ccd6f6',}}>Skillset</Nav.Link>
                    <Nav.Link fill="true" href="#contact" style={{color:'#ccd6f6',}} className="NavHeader">Contact</Nav.Link>
                    </Nav>
                    {/* <Form inline>
                    <Button variant="dark" style={{borderColor: '#64ffda',}}><a href={resume} className="NavHeader" style={{color: '#64ffda'}}>Resume</a></Button>
                    </Form> */}
                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default GlobalHeader;